.user-type-cards-wrapper{
 transform: translate(20%, 25%);
}
.type-card {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 70px 50px;
    max-width: 60%;
	border-radius: 10px;
    box-shadow: 4px 4px 15px rgb(0 0 0 / 20%);
}

h3 {
	font-family: "Fira Sans", sans-serif;
    font-weight: 700;
    font-size: 45px;
	margin-bottom: 5px;
	letter-spacing: 4px;
}
p{
	font-family: "Roboto", sans-serif;
	font-weight: 500;
}

.type-card.buisness{
	margin-top: 50px;
	background:#28ACEA;
	h3{
		color: #fff;
	}
	p{
		color: #fff;
	}
}

.icon-container .icon-arrow-forward{
	transform: scale(2);
}