.bookingCon{
	width: 90%;
	background-color: white;
	margin: 0 auto;
	padding: 30px 40px;
}
.services-deck-wrapper{
	display: grid;
	grid-template-columns: 1fr 1fr 1fr;
}
.services-selected{
	min-height: 415px;
}
p.subheading{
	font-size: 20px;
	margin-bottom: 0;
	font-family: "Fira Sans", sans-serif;
}
.login-hr.small-one-hr{
	width: 40px;
	height: 1px !important;
}

@media (max-width:600px) {
    .bookingCon{
        padding-left: 0px;
        width: 100%;
        display: grid;
        place-content: center;
    }
}